const Complete = (props) => {
  const { completeMsg, handleReset, outOfTime, puzzleLength, timedMode } =
    props;

  const buttonText =
    timedMode && puzzleLength < 7 && !outOfTime ? "Next level" : "Play again";

  return (
    <div className="complete-modal">
      <p className="complete-msg">{completeMsg}</p>
      <button onClick={handleReset} className="complete-btn">
        {buttonText}
      </button>
    </div>
  );
};

export default Complete;
