import "./App.css";
import React, { useState, useEffect } from "react";
import gear from "./icons/gear.png";
import Filter from "bad-words";
import Timer from "./components/Timer";
import Settings from "./components/Settings";
import KbRow from "./components/KbRow";
import Complete from "./components/Complete";
import Lines from "./components/Lines";
import names from "people-names";

const kbTop = ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"];
const kbMid = ["A", "S", "D", "F", "G", "H", "J", "K", "L"];
const kbBot = ["ENTER", "Z", "X", "C", "V", "B", "N", "M", "DEL"];

const filter = new Filter();
filter.removeWords("rape", "abuse");

function App() {
  const [maxAttempts, setmaxAttempts] = useState(5);
  const [puzzleLength, setPuzzleLength] = useState(5);
  const [currentAttempt, setCurrentAttempt] = useState(0);
  const [attemptsArray, setAttemptsArray] = useState(
    Array(maxAttempts).fill(Array(puzzleLength).fill(" "))
  );
  const [gameCount, setGameCount] = useState(0);
  const [wordsFetched, setWordsFetched] = useState(0);
  const [word, setWord] = useState([]);
  const [success, setSuccess] = useState(false);
  const [complete, setComplete] = useState(false);
  const [impossible, setImpossible] = useState([]);
  const [isInvalid, setIsInvalid] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [puzzleLengthChanged, setPuzzleLengthChanged] = useState(false);
  const [enterDisabled, setEnterDisabled] = useState(false);
  const [timedMode, setTimedMode] = useState(false);
  const [useTimer, setUseTimer] = useState(false);
  const [userInteracted, setUserInteracted] = useState(false);
  const [outOfTime, setOutOfTime] = useState(false);
  const [timerReset, setTimerReset] = useState(false);

  const handleShowSettings = () => {
    setShowSettings((prev) => !prev);
  };

  const handleAnswerInput = (e) => {
    setUserInteracted(true);
    const value = e.target.children[0].innerText;
    const current = attemptsArray[currentAttempt];
    const index = current.indexOf(" ");
    if (index === -1) return;
    setAttemptsArray((prev) => {
      const newArray = Array.from(prev.map((val) => val.map((arr) => arr)));
      newArray[currentAttempt][index] = value;
      return newArray;
    });
  };

  const handleDelete = () => {
    setUserInteracted(true);
    const current = attemptsArray[currentAttempt];
    let index = current.indexOf(" ");
    if (index === 0) return;
    if (index === -1) {
      index = puzzleLength;
    }
    setAttemptsArray((prev) => {
      const newArray = Array.from(prev.map((val) => val.map((arr) => arr)));
      newArray[currentAttempt][index - 1] = " ";
      return newArray;
    });
  };

  const handleAnswerSubmit = async () => {
    setEnterDisabled(true);
    let wordCheck = await checkWord();
    console.log(wordCheck);
    if (!wordCheck) {
      setIsInvalid(true);
      setTimeout(() => setIsInvalid(false), 1500);
      setEnterDisabled(false);
      return;
    }
    if (timedMode) {
      setUseTimer(false);
    }
    const current = attemptsArray[currentAttempt];
    current.forEach((value) => {
      if (!word.includes(value) && !impossible.includes(value)) {
        setImpossible((prev) => [...prev, value]);
      }
    });
    if (attemptsArray[currentAttempt].join("") === word.join("")) {
      setAttemptsArray((prev) => {
        return Array.from(prev.filter((_, index) => index === currentAttempt));
      });
      setSuccess(true);
      setComplete(true);
      if (useTimer) {
        setUseTimer(false);
      }
    } else {
      if (currentAttempt === maxAttempts - 1) {
        setComplete(true);
        if (timedMode) {
          setUseTimer(false);
        }
      } else {
        if (timedMode && !complete) {
          setUseTimer(true);
        }
      }
    }
    setEnterDisabled(false);
    setCurrentAttempt((prev) => {
      return prev + 1;
    });
    setUserInteracted(false);
  };

  const handleChangeWordLength = (e) => {
    const increment = e.target.innerText === "+" ? 1 : -1;
    if (
      (increment === 1 && puzzleLength > 6) ||
      (increment === -1 && puzzleLength < 5)
    ) {
      return;
    }
    setPuzzleLength((prev) => prev + increment);
    setPuzzleLengthChanged(true);
  };

  const checkWord = async () => {
    const inputWord = attemptsArray[currentAttempt].join("").toLowerCase();
    const url = `https://api.datamuse.com/words?sp=${inputWord}`;
    let valid = false;
    await (async () => {
      const response = await fetch(url);
      const res = await response.json();
      for (let i = 0; i < res.length; i++) {
        if (res[i].word === inputWord) {
          valid = true;
          console.log("true result from check");
          break;
        }
      }
    })();
    return valid;
  };

  const fetchWord = async () => {
    let alpha = [
      "a",
      "b",
      "c",
      "d",
      "e",
      "f",
      "g",
      "h",
      "i",
      "j",
      "k",
      "l",
      "m",
      "n",
      "o",
      "p",
      "q",
      "r",
      "s",
      "t",
      "u",
      "v",
      "w",
      "y",
      "z",
    ];
    let randomLetter = alpha[Math.floor(Math.random() * alpha.length)];
    let word = "";
    const url = `https://api.datamuse.com/words?sp=${randomLetter}${"?".repeat(
      puzzleLength - 1
    )}&max=300&md=fp`;
    console.log(url);
    try {
      let response = await fetch(url);
      let res = await response.json();
      res = res.filter(
        (val) =>
          !filter.clean(val.word).match(/[*]/) &&
          +val.tags[+val.tags.length - 1].replace("f:", "") > 10 &&
          !names.isPersonName(val.word) &&
          !val.tags.includes("u") &&
          !val.tags.includes("prop") &&
          !val.word.match(" ") &&
          !val.word.match(/[!"$%^&*?-_0-9]/)
      );
      if (res.length === 0) {
        return await fetchWord();
      }
      console.log("res", res);
      word = res[Math.floor(Math.random() * res.length)].word;
    } catch (err) {
      if (err) console.log(err);
    }

    return word.toUpperCase();
  };

  const handleReset = (n) => {
    const timedWin = timedMode && success && puzzleLength < 7;
    const timedLose = timedMode && complete && !success;
    if (timedWin) {
      setPuzzleLength((prev) => prev + 1);
    } else if (timedLose || (success && timedMode && puzzleLength === 7)) {
      setPuzzleLength(4);
    }
    setAttemptsArray(
      Array(maxAttempts).fill(
        Array(
          n === 4
            ? 4
            : n === 5
            ? 5
            : success && timedMode && puzzleLength === 7
            ? 4
            : timedWin
            ? puzzleLength + 1
            : timedLose
            ? 4
            : puzzleLength
        ).fill(" ")
      )
    );
    setCurrentAttempt(0);
    setImpossible([]);
    setComplete(false);
    setSuccess(false);
    setGameCount((prev) => prev + 1);
    setUserInteracted(false);
    setTimerReset(true);
    setTimeout(() => setTimerReset(false), 1000);
    setEnterDisabled(false);
    setOutOfTime(false);
  };

  useEffect(() => {
    if (timedMode && !userInteracted && !showSettings && !complete) {
      setAttemptsArray((prev) => {
        return prev.map((arr, index) =>
          arr.map((val, i) =>
            index >= currentAttempt && i == 0 ? word[i] : val
          )
        );
      });
    }

    if (timedMode && !showSettings && !useTimer && !complete) {
      setUseTimer(true);
    }

    if (wordsFetched === gameCount) {
      setWordsFetched((prev) => prev + 1);
      (async () => {
        const newWord = await fetchWord();
        console.log("new", newWord);
        setWord(newWord.split(""));
      })();
    }
    if (puzzleLengthChanged) {
      handleReset();
      setPuzzleLengthChanged(false);
    }
  }, [
    wordsFetched,
    gameCount,
    puzzleLengthChanged,
    timedMode,
    showSettings,
    useTimer,
    complete,
    word,
  ]);

  const handleOutOfTime = () => {
    setOutOfTime(true);
    setComplete(true);
    setUseTimer(false);
    setEnterDisabled(true);
  };

  const completeMsg =
    success && !timedMode
      ? "Well done! You found the word!"
      : success && timedMode && puzzleLength < 7
      ? "You found the word, now try the next level"
      : success && timedMode
      ? "Well done! You completed all timed levels!"
      : outOfTime
      ? `Out of time! The word was ${word.join("")}.`
      : `Unlucky! The word was ${word.join("")}. Better luck next time!`;

  return (
    <div className="App">
      <div
        className="background-modal"
        onClick={handleShowSettings}
        style={{ display: showSettings ? "block" : "none" }}
      />
      {timedMode && (
        <Timer
          useTimer={useTimer}
          handleOutOfTime={handleOutOfTime}
          timerReset={timerReset}
          complete={complete}
        />
      )}
      <img onClick={handleShowSettings} src={gear} className="gear-icon" />
      <Settings
        showSettings={showSettings}
        puzzleLength={puzzleLength}
        handleChangeWordLength={handleChangeWordLength}
        setTimedMode={setTimedMode}
        timedMode={timedMode}
        setPuzzleLength={setPuzzleLength}
        handleReset={handleReset}
        setUseTimer={setUseTimer}
      />
      <div className="title no-select">Lexical</div>
      <div className="invalid" style={{ display: isInvalid ? "flex" : "none" }}>
        Word is not valid
      </div>
      <div className="game-section">
        <Lines
          attemptsArray={attemptsArray}
          currentAttempt={currentAttempt}
          puzzleLength={puzzleLength}
          isInvalid={isInvalid}
          success={success}
          word={word}
          complete={complete}
        />
      </div>
      <div className="input">
        <KbRow
          row={kbTop}
          attemptsArray={attemptsArray}
          currentAttempt={currentAttempt}
          handleDelete={handleDelete}
          enterDisabled={enterDisabled}
          handleAnswerSubmit={handleAnswerSubmit}
          handleAnswerInput={handleAnswerInput}
          impossible={impossible}
        />
        <KbRow
          row={kbMid}
          attemptsArray={attemptsArray}
          currentAttempt={currentAttempt}
          handleDelete={handleDelete}
          enterDisabled={enterDisabled}
          handleAnswerSubmit={handleAnswerSubmit}
          handleAnswerInput={handleAnswerInput}
          impossible={impossible}
        />
        <KbRow
          row={kbBot}
          attemptsArray={attemptsArray}
          currentAttempt={currentAttempt}
          handleDelete={handleDelete}
          enterDisabled={enterDisabled}
          handleAnswerSubmit={handleAnswerSubmit}
          handleAnswerInput={handleAnswerInput}
          impossible={impossible}
        />
      </div>
      {complete && (
        <Complete
          handleReset={handleReset}
          outOfTime={outOfTime}
          completeMsg={completeMsg}
          timedMode={timedMode}
          puzzleLength={puzzleLength}
        />
      )}
    </div>
  );
}

export default App;
