import { useEffect, useState } from "react";

const Settings = (props) => {
  const [timedModeOn, setTimedModeOn] = useState(false);
  const [needsUpdate, setNeedsUpdate] = useState(false);

  useEffect(() => {
    if (timedModeOn && needsUpdate) {
      props.setTimedMode(!props.timedMode);
      props.setPuzzleLength(4);
      props.handleReset(4);
      setNeedsUpdate(false);
    }

    if (!timedModeOn && needsUpdate) {
      props.setTimedMode(!props.timedMode);
      props.setPuzzleLength(5);
      props.handleReset(5);
      props.setUseTimer(false);
      setNeedsUpdate(false);
    }
  }, [timedModeOn, needsUpdate]);

  return (
    <div
      className="settings-menu"
      style={{ display: props.showSettings ? "block" : "none" }}
    >
      <div className="word-length-wrapper">
        <p>Word length</p>
        <div className="word-length">{props.puzzleLength}</div>
        <div className="change-word-length-wrapper">
          <button
            className="change-word-length-btn"
            onClick={props.handleChangeWordLength}
          >
            
          </button>
          <button
            className="change-word-length-btn"
            onClick={props.handleChangeWordLength}
          >
            +
          </button>
        </div>
      </div>
      <div className="line"></div>
      <div className="timed-game-wrapper">
        <p>Timed Game</p>
        <div className="switch-wrapper">
          <div className="backing on-backing">ON</div>
          <div className="backing off-backing">OFF</div>
          <div
            className={`switch ${props.timedMode && "timed-on"}`}
            onClick={() => {
              setTimedModeOn((prev) => !prev);
              setNeedsUpdate(true);
            }}
          ></div>
        </div>
      </div>
      <p className="settings-msg">Changing these setting will reset the word</p>
    </div>
  );
};

export default Settings;
