const Lines = (props) => {
  const {
    attemptsArray,
    currentAttempt,
    puzzleLength,
    isInvalid,
    success,
    word,
    complete,
  } = props;

  const isCorrect = (letter, index) => {
    return letter === word[index];
  };

  const isPresent = (wholeWord, i) => {
    const letter = wholeWord[i];
    const otherCorrect = wholeWord.filter(
      (val, index) => index !== i && val === letter && val === word[index]
    ).length;
    const alreadyUsed = wholeWord.filter(
      (val, index) => val === letter && index < i && val !== word[index]
    ).length;
    return (
      word.filter((val) => val === letter).length - alreadyUsed - otherCorrect >
      0
    );
  };

  return Array(attemptsArray.length)
    .fill()
    .map((_, index) => (
      <div
        key={index}
        className={`attempt-line ${index === currentAttempt && "current"} ${
          index < currentAttempt && "shrink"
        }`}
        style={{ display: index > currentAttempt ? "none" : "flex" }}
      >
        {Array(puzzleLength)
          .fill()
          .map((_, i) => (
            <div
              className={`letter-square no-select ${
                currentAttempt > index && isCorrect(attemptsArray[index][i], i)
                  ? "correct"
                  : currentAttempt > index && isPresent(attemptsArray[index], i)
                  ? "present"
                  : "false"
              } ${index === currentAttempt && isInvalid && "shake"} ${
                complete && success && "bounce"
              }`}
              key={i}
            >
              <p className="letter">{attemptsArray[index][i]}</p>
            </div>
          ))}
      </div>
    ));
};

export default Lines;
