const KbRow = (props) => {
  const {
    attemptsArray,
    currentAttempt,
    handleDelete,
    enterDisabled,
    handleAnswerSubmit,
    handleAnswerInput,
    impossible,
  } = props;

  return (
    <div className="kb-row">
      {props.row.map((val, index) => {
        return (
          <button
            className={`key ${val}`}
            key={index}
            disabled={
              impossible.includes(val) ||
              (val === "ENTER" && enterDisabled) ||
              (val === "ENTER" &&
                attemptsArray[currentAttempt] &&
                attemptsArray[currentAttempt].includes(" ")) ||
              (val === "DEL" &&
                attemptsArray[currentAttempt] &&
                attemptsArray[currentAttempt][0] === " ")
            }
            onClick={
              val === "DEL"
                ? handleDelete
                : val === "ENTER"
                ? handleAnswerSubmit
                : handleAnswerInput
            }
          >
            <p className="key-text no-select">{val}</p>
          </button>
        );
      })}
    </div>
  );
};

export default KbRow;
