import stopwatch from "../icons/stopwatch.png";
import { useState, useEffect } from "react";

const Timer = (props) => {
  const [timerTime, setTimerTime] = useState(30);
  const [timeRemaining, setTimeRemaining] = useState(timerTime);
  const [timerRunning, setTimerRunning] = useState(false);
  const [timerSet, setTimerSet] = useState(false);
  const [timerInterval, setTimerInterval] = useState(undefined);
  const [timerElapsed, setTimerElapsed] = useState(false);

  const screenWidth = window.screen.availWidth;

  useEffect(() => {
    const update = () => {
      setTimeRemaining((prev) => prev - 1);
    };

    if (props.timerReset && timerElapsed) {
      setTimeRemaining(timerTime);
      setTimerElapsed(false);
    }

    if (props.useTimer && !timerSet && !timerElapsed) {
      setTimeRemaining(timerTime);
      setTimerSet(true);
    }

    if (!props.useTimer && timerRunning && !props.complete) {
      clearInterval(timerInterval);
      setTimerRunning(false);
      setTimerSet(false);
    }

    if (!props.useTimer && timerRunning && props.complete) {
      clearInterval(timerInterval);
      setTimerRunning(false);
      setTimerSet(false);
      setTimerElapsed(true);
    }

    if (timerSet && !timerRunning) {
      setTimerInterval(setInterval(update, 1000));
      setTimerRunning(true);
    }
    if (timerRunning && timeRemaining === 0) {
      clearInterval(timerInterval);
      setTimerRunning(false);
      setTimerSet(false);
      setTimerElapsed(true);
      props.handleOutOfTime();
    }

    return () => {};
  }, [timerSet, timeRemaining, timerElapsed, props.useTimer, props.timerReset]);

  const green = "linear-gradient(to left, rgb(63, 206, 122), rgb(8, 153, 57))";
  const yellow =
    "linear-gradient(to left, rgb(206, 204, 63), rgb(187, 160, 9))";
  const red = "linear-gradient(to left, rgb(206, 96, 63), rgb(175, 72, 58))";

  return (
    <div className="timer-component">
      <img src={stopwatch} className="timer-icon" />
      <div className="timer-wrapper">
        <div className="timer">
          <div
            className="timer-inner"
            style={
              screenWidth > 1150
                ? {
                    height: `${
                      (timeRemaining / timerTime) * 100 -
                      Math.abs(timeRemaining - timerTime) / 9.009
                    }%`,
                    background:
                      timeRemaining > 9
                        ? green
                        : timeRemaining > 3
                        ? yellow
                        : red,
                  }
                : {
                    width: `${
                      (timeRemaining / timerTime) * 100 -
                      Math.abs(timeRemaining - timerTime) / 6
                    }%`,
                    background:
                      timeRemaining > 9
                        ? green
                        : timeRemaining > 3
                        ? yellow
                        : red,
                  }
            }
          ></div>
        </div>
      </div>
      <div className="time-left">{timeRemaining}</div>
    </div>
  );
};

export default Timer;
